<template>
  <div class="dragBox-contaner" :style="{width: width, height: height}">
    <div
    class="dragBox-topDrag"
    v-if="topDrag"
    @mousedown="dragStart($event, 'top')">
      <dragSignalRow></dragSignalRow>
    </div>
    <div class="dragBox-middleBox">
      <div
      class="dragBox-leftDrag"
      v-if="leftDrag"
      @mousedown="dragStart($event, 'left')">
        <dragSignalColumn></dragSignalColumn>
      </div>
      <div class="dragBox-display">
        <slot></slot>
      </div>
      <div
      class="dragBox-rightDrag"
      @mousedown="dragStart($event, 'right')"
      v-if="rightDrag">
        <dragSignalColumn></dragSignalColumn>
      </div>
    </div>
    <div
    class="dragBox-bottomDrag"
    v-if="bottomDrag"
    @mousedown="dragStart($event, 'bottom')">
      <dragSignalRow></dragSignalRow>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    sign: { // 标识
      type: String
    },
    width: { // 组件宽度
      type: String,
      default: '100%'
    },
    height: { // 组件高度
      type: String,
      default: '100%'
    },
    topDrag: { // 开启顶部拖拽
      type: Boolean,
      default: false
    },
    leftDrag: { // 开启左拖拽
      type: Boolean,
      default: false
    },
    rightDrag: { // 开启右拖拽
      type: Boolean,
      default: false
    },
    bottomDrag: { // 开启顶部拖拽
      type: Boolean,
      default: false
    }
  },
  components: {
    // 横向拖拽标记
    dragSignalRow: {
      template: `
      <div class="dragBox-dragSignalRow">
        <div></div>
        <div></div>
        <div></div>
      </div>
      `
    },
    // 纵向拖拽标记
    dragSignalColumn: {
      template: `
      <div class="dragBox-dragSignalColumn">
        <div></div>
        <div></div>
        <div></div>
      </div>
      `
    }
  },
  data () {
    return {
    }
  },
  methods: {
    // 拖动开始
    dragStart ($event, cursor) {
      if (cursor === 'right' || cursor === 'left') {
        this.$store.commit('setDragBoxMaskStatus', {
          status: true,
          cursor: 'ew-resize',
          mouseX: $event.clientX,
          mouseY: $event.clientY,
          sign: this.sign
        })
      } else {
        console.log(1)
        this.$store.commit('setDragBoxMaskStatus', {
          status: true,
          cursor: 'ns-resize',
          mouseX: $event.clientX,
          mouseY: $event.clientY,
          sign: this.sign
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
@dragSignalSize: 20px;
.dragBox-contaner {
  .flexLayout(column, center, center);
  overflow: hidden;
  > .dragBox-middleBox {
    .boxSetting();
    .flexLayout();
    > .dragBox-display {
      .boxSetting();
      flex: 1;
    }
    > .dragBox-leftDrag, > .dragBox-rightDrag {
      .boxSetting(unset);
     .flexLayout(column, center, center);
    }
  }

  // 拖拽标志
  /deep/ .dragBox-dragSignalRow  {
    .boxSetting(100%, @dragSignalSize);
    .flexLayout(row, center, center);
    cursor: ns-resize;
    .transitionSetting(
      @hoverDo: {
        background: @themeHover-color;
      },
      @activeDo: {
        background: @theme-color;
      },
      @hoverElem: div,
      @activeElem: div
    );
    > div {
      .boxSetting(5px, 5px, 0, 2.5px);
      .transitionSetting();
      background: @placeHolderText-color;
    }
  }
  /deep/ .dragBox-dragSignalColumn {
    .boxSetting(@dragSignalSize);
    .flexLayout(column, center, center);
    cursor: ew-resize;
    .transitionSetting(
      @hoverDo: {
        background: @themeHover-color;
      },
      @activeDo: {
        background: @theme-color;
      },
      @hoverElem: div,
      @activeElem: div
    );
    > div {
      .boxSetting(5px, 5px, 0, 2.5px);
      .transitionSetting();
      background: @placeHolderText-color;
    }
  }
}
</style>
