module.exports = {
  state: {
    sign: '', // 标识
    mouseX: '', // 鼠标x的位置
    mouseY: '', // 鼠标y的位置
    mousemoveX: '', // 鼠标移动x的位置
    mousemoveY: '', // 鼠标移动y的位置
    dragBoxMaskStatus: false, // 拖拽遮罩状态
    dragBoxMaskCursor: 'ew-resize' // 遮罩鼠标指针状态
  },
  getters: {
    // 获取拖拽层标记
    getDragSign (state) {
      return state.sign
    },
    // 获取拖拽遮罩状态
    getDragBoxMaskStatus (state) {
      return state.dragBoxMaskStatus
    },
    // 获取遮罩鼠标指针状态
    getDragBoxMaskCursor (state) {
      return state.dragBoxMaskCursor
    }
  },
  mutations: {
    // 遮罩层设置
    setDragBoxMaskStatus (state, setting) {
      state.dragBoxMaskStatus = setting.status
      state.dragBoxMaskCursor = setting.cursor
      state.mouseX = setting.mouseX || ''
      state.mouseY = setting.mouseY || ''
      state.sign = setting.sign || ''
    },
    // 设置移动位置
    setMovePosition (state, setting) {
      state.mousemoveX = setting.mousemoveX - state.mouseX
      state.mousemoveY = setting.mousemoveY - state.mouseY
    }
  },
  actions: {
  }
}
