<template>
  <div class="eidtor-container">
    <!-- 顶部导航模块 STR -->
    <div class="editor-top" :style="{height: $store.getters.getTopNavHeight}">
      <top-nav></top-nav>
    </div>
    <!-- 顶部导航模块 END -->
    <div class="editor-middle">
      <!-- 左侧模块 STR -->
      <drag-box rightDrag :width="leftDragBoxWidth" :sign="'left'">
        <template>
          <left-nav></left-nav>
        </template>
      </drag-box>
      <!-- 左侧模块 END -->
      <!-- 中央视图区 STR -->
      <div class="editor-view" ref="editor-view" :style="{width: `${editorViewWidth}px`, height: `${editorViewHeight}px`}">
        <editor-view :width="editorViewWidth" :height="editorViewHeight"></editor-view>
      </div>
      <!-- 中央视图区 END -->
      <!-- 右侧模块 STR -->
      <drag-box leftDrag :width="rightDragBoxWidth" :sign="'right'">
        <template>
          <right-nav></right-nav>
        </template>
      </drag-box>
      <!-- 右侧模块 END -->
    </div>
    <!-- 底部模块 STR -->
    <div class="editor-bottom" ref="bottomBox">
      <drag-box topDrag :height="bottomDragBoxHeight" :sign="'bottom'">
        <template>

        </template>
      </drag-box>
    </div>
    <!-- 底部模块 END -->
    <drag-box-mask></drag-box-mask>
    <!-- 公共拖拽遮罩层 STR -->
  </div>
</template>

<script>
import dragBox from '../components/dragBox' // 拖拽盒子
import dragBoxMask from '../components/dragBoxMask' // 拖拽盒子遮罩

import topNav from './topNav' // 顶部导航
import leftNav from './leftNav' // 左侧导航
import rightNav from './rightNav' // 右侧导航
// 异步调用更换加载生命周期
const editorView = () => import('./editorView')// 编辑试图区
export default {
  components: {
    dragBox, // 拖拽盒子
    dragBoxMask, // 拖拽遮罩
    topNav, // 顶部导航
    leftNav, // 左侧导航
    rightNav, // 右侧导航
    editorView // 编辑试图区
  },
  data () {
    return {
      rightDragBoxSetting: '', // 右侧拖拽盒子设置
      rightDragBoxSizeBuffer: '', // 右侧拖拽盒子缓存信息
      leftDragBoxSetting: '', // 左侧拖拽盒子设置
      leftDragBoxSizeBuffer: '', // 左侧拖拽盒子缓存信息
      bottomDragBoxSetting: '', // 底部拖拽盒子设置
      bottomDragBoxSizeButter: '', // 底部拖拽盒子缓存信息
      boxWatch: '', // 拖拽盒子监听缓存
      editorViewWidth: '', // 视图区宽度
      editorViewHeight: '' // 视图区高度
    }
  },
  computed: {
    // 右侧距离
    rightDragBoxWidth () {
      return `${this.rightDragBoxSetting.size}px`
    },
    // 左侧距离
    leftDragBoxWidth () {
      return `${this.leftDragBoxSetting.size}px`
    },
    // 下方距离
    bottomDragBoxHeight () {
      return `${this.bottomDragBoxSetting.size}px`
    }
  },
  methods: {
    // 初始化数值
    init () {
      this.rightDragBoxSetting = {
        size: 320,
        max: 600,
        min: 200
      }
      this.leftDragBoxSetting = {
        size: 320,
        max: 600,
        min: 200
      }
      this.bottomDragBoxSetting = {
        size: 250,
        max: 600,
        min: 200
      }
      this.rightDragBoxSizeBuffer = this.rightDragBoxSetting.size
      this.leftDragBoxSizeBuffer = this.leftDragBoxSetting.size
      this.bottomDragBoxSizeButter = this.bottomDragBoxSetting.size
    },
    // 重置editor-view的style
    resetEditorViewSize () {
      this.editorViewWidth = window.innerWidth - (this.leftDragBoxSetting.size + this.rightDragBoxSetting.size)
      this.editorViewHeight = window.innerHeight - (this.bottomDragBoxSetting.size + this.$store.getters.getTopNavHeight)
    }
  },
  watch: {
    // 监听$store标志变形
    '$store.state.dragBox.sign' (newVal, oldVal) {
      if (newVal) {
        if (newVal === 'left') {
          this.boxWatch = this.$watch('$store.state.dragBox.mousemoveX', (x) => {
            let changeSize = this.leftDragBoxSizeBuffer + x
            if (changeSize > this.leftDragBoxSetting.max) {
              changeSize = this.leftDragBoxSetting.max
            }
            if (changeSize < this.leftDragBoxSetting.min) {
              changeSize = this.leftDragBoxSetting.min
            }
            this.leftDragBoxSetting.size = changeSize
            // 每次变动都去重置大小
            this.resetEditorViewSize()
          })
        } else if (newVal === 'right') {
          this.boxWatch = this.$watch('$store.state.dragBox.mousemoveX', (x) => {
            let changeSize = this.rightDragBoxSizeBuffer - x
            if (changeSize > this.rightDragBoxSetting.max) {
              changeSize = this.rightDragBoxSetting.max
            }
            if (changeSize < this.rightDragBoxSetting.min) {
              changeSize = this.rightDragBoxSetting.min
            }
            this.rightDragBoxSetting.size = changeSize
            // 每次变动都去重置大小
            this.resetEditorViewSize()
          })
        } else if (newVal === 'bottom') {
          this.boxWatch = this.$watch('$store.state.dragBox.mousemoveY', (y) => {
            let changeSize = this.bottomDragBoxSizeButter - y
            if (changeSize > this.bottomDragBoxSetting.max) {
              changeSize = this.bottomDragBoxSetting.max
            }
            if (changeSize < this.bottomDragBoxSetting.min) {
              changeSize = this.bottomDragBoxSetting.min
            }
            this.bottomDragBoxSetting.size = changeSize
            // 每次变动都去重置大小
            this.resetEditorViewSize()
          })
        }
      } else {
        this.boxWatch()
        if (oldVal === 'left') {
          this.leftDragBoxSizeBuffer = this.leftDragBoxSetting.size
        } else if (oldVal === 'right') {
          this.rightDragBoxSizeBuffer = this.rightDragBoxSetting.size
        } else if (oldVal === 'bottom') {
          this.bottomDragBoxSizeButter = this.bottomDragBoxSetting.size
        }
      }
    }
  },
  created () {
    // 初始化信息
    this.init()
  },
  mounted () {
    this.resetEditorViewSize()
    // 绑定浏览器窗口变化函数
    window.onresize = () => {
      this.resetEditorViewSize()
    }
  }
}
</script>

<style lang="less" scoped>
.eidtor-container {
  overflow: hidden;
  .boxSetting();
  .flexLayout(column, flex-start, center);
  > .editor-top {
    .boxSetting();
  }
  > .editor-middle {
    .boxSetting(100%, 100%);
    .flexLayout();
    > .editor-view {
      position: relative;
      .boxSetting();
      flex: 1;
    }
  }
  > .editor-bottom {
    .boxSetting(100%, unset);
  }
}
</style>
