<template>
  <div
  class="dragBoxMask-container"
  v-show="draging"
  @mouseup="dragEnd"
  @mousemove="mousemove"
  :style="{cursor: dragCursor}"
  ></div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  computed: {
    // 开启遮罩
    draging () {
      return this.$store.getters.getDragBoxMaskStatus
    },
    // 遮罩层指针
    dragCursor () {
      return this.$store.getters.getDragBoxMaskCursor
    }
  },
  methods: {
    // 拖动结束
    dragEnd ($event) {
      this.$store.commit('setDragBoxMaskStatus', {
        status: false,
        cursor: 'unset'
      })
    },
    // 鼠标移动
    mousemove ($event) {
      this.$store.commit('setMovePosition', {
        mousemoveX: $event.clientX,
        mousemoveY: $event.clientY
      })
    }
  }
}
</script>

<style lang="less" scoped>
.dragBoxMask-container {
  .boxSetting();
  .fixedPosition();
}
</style>
